import { useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../context/AuthContext";
import { users } from "../data/users";
import "../App.css"
function Login() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const navigate = useNavigate();
  const { login } = useContext(AuthContext);

  const handleSubmit = (e) => {
    e.preventDefault();

    // Kim tra thng tin ng nhp
    const user = users.find(
      (u) => u.username === username && u.password === password
    );

    if (user) {
      // To token gi (trong thc t, token s c to t backend)
      const fakeToken = btoa(`${username}:${password}`);

      // Lu thng tin ng nhp
      login(fakeToken, {
        id: user.id,
        username: user.username,
        role: user.role
      });

      // Chuyn hng n trang home
      navigate("/home");
    } else {
      setError("Tn ng nhp hoc mt khu khng ng!");
    }
  };

  return (
    <div className="login-container">
      <form onSubmit={handleSubmit}>
        <h1>Qun l vt t tiu hao CNTT</h1>
        <h2>ng nhp</h2>
        {error && <div className="error">{error}</div>}
        <div>
          <label>Tn ng nhp:</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
            placeholder="in tn ng nhp"
          />
        </div>
        <div>
          <label>Mt khu:</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
            placeholder="in mt khu"
          />
        </div>
        <button type="submit">ng nhp</button>
      </form>

    </div>
  );
}


export default Login;