import React, { useEffect, useState } from "react";
import "./addproduct.css";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import toast from "react-hot-toast";
// import Header from "./components/Header"; // Import Header component

const AddProduct = () => {

  const products = {
    key: "",
    name: "",
    category: "",
    quantity: "",
    price: "",
  };
  const [product, setProduct] = useState(products);
  const [categories, setCategories] = useState([]); // Danh sch danh mc

  const navigate = useNavigate();

  // const inputHandler = (e) => {
  //   const { name, value } = e.target;
  //   console.log(name, value);

  //   setProduct({ ...product, [name]: value });
  // };

  const inputHandler = (e) => {
    const { name, value } = e.target;

    if (name === 'category') {
      // Tm tn danh mc da trn id  chn
      const selectedCategory = categories.find(cat => cat._id === value);
      setProduct({
        ...product,
        [name]: value,   // Lu _id ca category
        categoryName: selectedCategory ? selectedCategory.name : '', // Lu tn category
      });
    } else {
      setProduct({ ...product, [name]: value });
    }
  };


  // Ly danh sch danh mc t API
  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const response = await axios.get("https://cntt-supply.onrender.com/api/categories"); // ng dn API ly danh mc
        setCategories(response.data); // Lu danh mc vo state
      } catch (error) {
        console.error("Error fetching categories:", error);
      }
    };
    fetchCategories();
  }, []);


  // const submitForm = async (e) => {
  //   e.preventDefault();

  //   // Kim tra nu cc trng bt buc cha c in
  //   if (!product.key || !product.category || !product.quantity) {
  //     toast.error("Vui lng in y  cc trng bt buc!", { position: "top-right" });
  //     return;
  //   }

  //   // Kim tra nu quantity khng phi l mt s hp l v ln hn 0
  //   if (isNaN(product.quantity) || product.quantity <= 0) {
  //     toast.error("S lng phi l mt s hp l v ln hn 0!", { position: "top-right" });
  //     return;
  //   }

  //   console.log("Form submitted:", product); // Kim tra d liu

  //   await axios
  //     .post("http://localhost:8000/api/product", product)
  //     .then((response) => {
  //       toast.success(response.data.message, { position: "top-right" });
  //       navigate("/products");
  //     })
  //     .catch((error) => {
  //       console.log(error);
  //     });
  // };

  const submitForm = async (e) => {
    e.preventDefault();

    // Kim tra nu cc trng bt buc cha c in
    if (!product.key || !product.name || !product.category || !product.quantity || !product.price) {
      toast.error("Vui lng in y  cc trng bt buc!", { position: "top-right" });
      return;
    }

    // Kim tra nu quantity khng phi l mt s hp l v ln hn 0
    if (isNaN(product.quantity) || product.quantity <= 0) {
      toast.error("S lng phi l mt s hp l v ln hn 0!", { position: "top-right" });
      return;
    }

    // Kim tra nu price khng phi l mt s hp l v ln hn 0
    if (isNaN(product.price) || product.price <= 0) {
      toast.error("Gi phi l mt s hp l v ln hn 0!", { position: "top-right" });
      return;
    }

    // Kim tra nu key c phi l mt chui hp l
    if (!product.key.trim()) {
      toast.error("M sn phm khng c  trng!", { position: "top-right" });
      return;
    }

    // Log d liu  kim tra trc khi gi
    console.log("Form submitted:", product);

    try {
      // Gi request ti API  to sn phm mi
      const response = await axios.post("https://cntt-supply.onrender.com/api/product", product);
      toast.success(response.data.message, { position: "top-right" });
      navigate("/products");
    } catch (error) {
      console.log("Error submitting form:", error);
      toast.error(" c li xy ra, vui lng th li!", { position: "top-right" });
    }
  };


  return (

    <div className="addProduct">
      <Link to="/products" type="button" class="btn btn-secondary">
        <i class="fa-solid fa-backward"></i> Quay li
      </Link>

      <h3>THM MI SN PHM</h3>
      <form className="addProductForm" onSubmit={submitForm}>
        <div className="inputGroup">
          <label htmlFor="key">M sn phm:</label>
          <input
            type="text"
            id="key"
            onChange={inputHandler}
            name="key"
            autoComplete="off"
            placeholder="Nhp M sn phm"
          />
        </div>
        <div className="inputGroup">
          <label htmlFor="name">Tn sn phm:</label>
          <input
            type="text"
            id="name"
            onChange={inputHandler}
            name="name"
            autoComplete="off"
            placeholder="Nhp Tn sn phm"
          />
        </div>
        <div className="inputGroup">
          <label htmlFor="category">Loi sn phm</label>
          <select
            id="category"
            name="category"
            value={product.category || ""}
            onChange={inputHandler}
          >
            <option value="">Chn Loi sn phm</option>
            {categories.map((cat) => (
              <option key={cat._id} value={cat._id}>
                {cat.name}
              </option>
            ))}
          </select>
        </div>
        <div className="inputGroup">
          <label htmlFor="quantity">S lng:</label>
          <input
            type="text"
            id="quantity"
            onChange={inputHandler}
            name="quantity"
            autoComplete="off"
            placeholder="Nhp S lng"
          />
        </div>
        <div className="inputGroup">
          <label htmlFor="price">Gi tin:</label>
          <input
            type="text"
            id="price"
            onChange={inputHandler}
            name="price"
            autoComplete="off"
            placeholder="Nhp Gi tin"
          />
        </div>
        <div className="inputGroup">
          <button type="submit" class="btn btn-primary">
            THM MI
          </button>
        </div>
      </form>
    </div>
  );
};

export default AddProduct;
